































import { Component,  Vue } from 'vue-property-decorator';
import Home from "@/components/Home.vue";
import Service from "@/components/Service.vue";
import OurTeam from "@/components/OurTeam.vue";
import OurGallery from "@/components/OurGallery.vue";
import OurPrice from "@/components/OurPrice.vue";
import OurNews from "@/components/OurNews.vue";
import OurContact from "@/components/OurContact.vue";
import OurFooter from "@/components/OurFooter.vue";

@Component({
    components: {
        NavBarHeader,
        Home,
        Service,
        OurTeam,
        OurGallery,
        OurPrice,
        OurNews,
        OurContact,
        OurFooter
    }
})
export default class NavBarHeader extends Vue {

    barItem: {title: string, hide: boolean, active: boolean, mobileScroll: boolean}|null = null;
    navBarItems: Array<any> = [
        {title: 'HOME', hide: true, active: false, scroll: 0, mobileScroll:0},
        {title: 'SERVICE', hide: true, active: false, scroll: 800, mobileScroll:600},
        {title: 'TEAM', hide: true, active: false, scroll: 1400, mobileScroll:2400},
        {title: 'GALLERY', hide: true, active: false, scroll: 2500, mobileScroll:4450},
        {title: 'PRICE', hide: true, active: false, scroll: 3600, mobileScroll:10650},
        {title: 'NEWS', hide: true, active: false, scroll: 4500, mobileScroll:12800},
        {title: 'CONTACT', hide: true, active: false, scroll: 5300, mobileScroll:13470}
    ];
    verticalNavBar: boolean = false;

    showRedLine(item:any) {
        item.hide = !item.hide;
    }

    activateNavItem(item:any) {
        this.navBarItems.forEach(item => item.active = false);
        item.active = !item.active;
        window.scrollTo(0, item.scroll);
    }

    activateVerticalNavItem(item:any) {
        this.navBarItems.forEach(item => item.active = false);
        item.active = !item.active;
        window.scrollTo(0, item.mobileScroll);
    }

    showVerticalMenu() {
        this.verticalNavBar = !this.verticalNavBar;
    }

    handleScroll (evt:any, el:any) {
        this.navBarItems.forEach(item => {
            if (window.scrollY >= item.scroll) {
                this.navBarItems.forEach(item => item.active = false);
                item.active = !item.active;
            }
        })
    }
}
